* {
  margin: 0;
  padding: 0;

  --component-distance: 25px;
  --page-margin: 10vw;
}

.App {
  position: fixed;
  
  width: 100vw;
  height: 100vh;

  font-size: 20px;
  text-align: center;
}

.block {
  width: 100%;
  margin: var(--component-distance) var(--page-margin) 0;
}

.chord {
  margin: 100px 0;
}